<template>
    <transition :name="transitionName" mode="out-in">
        <router-view/>
    </transition>
</template>

<script>
	export default {
		name: 'app',
		data() {
			return {
				transitionName: 'fade',
			}
		},
		created() {
			this.$router.beforeEach((to, from, next) => {
				let transitionName = to.meta.transitionName;

				this.transitionName = transitionName || 'fade';

				next();
			});
		},
	}
</script>

