    <div id="home">

        <div style="grid-template-columns: 1.3fr 1fr; display: grid">
            <div style="border-right: 2px dashed #a76ff6; margin-top: 18px; padding: 20px; height: 89vh;">
                <h1 id="topic">{{topic.title}}</h1>
                <p class="small left-text">{{topic.desc}}</p>
                <label for="editor"></label><textarea id="editor" @input="slateEdits" v-text="slate">
                </textarea>
                <div class="footer">
                    <p class="small left-text" >Word Limit: ({{words}}/140)</p>
                    <a class="btn">Submit your changes</a>  <a class="btn">Change the topic</a>
                </div>
            </div>
            <div>
                <h1>Info</h1>
