</template>

<script>
    import EditsTree from "../components/EditsTree.vue";
	export default {
		name: "Home",
		data() {
			return {
				words: 0,
				slate: '',
				topic: {
					desc: 'This si the topic descriptionsThis si the topic descriptionsThis si the topic descriptionsThis si the topic descriptions',
					title: 'This is the topic'
				}
			}
		},
		methods: {
			slateEdits: function () {
				this.slate = document.getElementById('editor').value;
				if (this.slate !== '') {
					this.words = this.slate.trim().split(/\s+/).length;
				} else {
					this.words = 0;
				}
			}
		},
		components: {
			EditsTree
		}
	}
</script>

