import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import About from './views/About.vue'

Vue.use(Router);

export default new Router({
	mode: 'history',
	base: process.env.BASE_URL,
	routes: [
		{
			path: '/',
			name: 'Home',
			component: Home,
			meta: {
				transitionName: 'fade'
			}
		},
		{
			path: '/about',
			name: 'About',
			component: About,
			meta: {
				transitionName: 'fade'
			}
		}
	]
})
